import { create } from "zustand";

const API_KEY = "9f53add88a37ef9d5289abbbba6e6f8b";
const checkIsMobile = () => window.innerWidth <= 800;

const getBasePath = () => {
  if (typeof window !== "undefined") {
    const pathSegments = window.location.pathname.split("/").filter(Boolean);
    return pathSegments.length > 0 ? `/${pathSegments[0]}` : "";
  }
  return "";
};

const useStore = create((set) => ({
  User: localStorage.getItem("User") || "Guest",
  List: JSON.parse(localStorage.getItem("TodoList")) || [],
  date: localStorage.getItem("date")
    ? new Date(localStorage.getItem("date"))
    : new Date(),
  watherBar: 0,
  basePath: getBasePath(),
  updateBasePath: () => set({ basePath: getBasePath() }),
  isMobile: checkIsMobile(),

  updateIsMobile: () => {
    set({ isMobile: checkIsMobile() });
  },

  setList: (newText) => {
    set((state) => {
      const newItem = { text: newText, checked: false };
      const updatedList = [...state.List, newItem];
      localStorage.setItem("TodoList", JSON.stringify(updatedList));
      return { List: updatedList };
    });
  },

  delList: (index) => {
    set((state) => {
      const updatedList = state.List.filter((_, i) => i !== index);
      localStorage.setItem("TodoList", JSON.stringify(updatedList));
      return { List: updatedList };
    });
  },

  toggleCheck: (index) => {
    set((state) => {
      const updatedList = state.List.map((item, i) =>
        i === index ? { ...item, checked: !item.checked } : item
      );
      localStorage.setItem("TodoList", JSON.stringify(updatedList));
      return { List: updatedList };
    });
  },

  setUser: (newUser) => {
    localStorage.setItem("User", newUser);
    set({ User: newUser });
  },

  setDate: (newDate) => {
    localStorage.setItem("date", newDate.toString());
    set({ date: newDate });
  },

  setwatherBar: (wather) => set({ watherBar: wather }),

  startwatherBar: () => {
    const interval = setInterval(() => {
      set((state) => {
        if (state.progress < 100) {
          return { progress: state.progress + 10 };
        } else {
          clearInterval(interval);
          return { progress: 100 };
        }
      });
    }, 100);
  },

  currentTime: new Date(),

  temperature: {
    current: null,
    low: null,
    high: null,
    location: "",
  },

  updateCurrentTime: () => {
    set({ currentTime: new Date() });
  },

  initWeather: async () => {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(
        async (position) => {
          try {
            const { latitude, longitude } = position.coords;
            localStorage.setItem(
              "coords",
              JSON.stringify({ latitude, longitude })
            );

            const response = await fetch(
              `https://api.openweathermap.org/data/2.5/weather?lat=${latitude}&lon=${longitude}&appid=${API_KEY}&units=metric`
            );

            const data = await response.json();
            set({
              temperature: {
                current: Math.round(data.main.temp * 10) / 10,
                low: Math.round(data.main.temp_min * 10) / 10,
                high: Math.round(data.main.temp_max * 10) / 10,
                location: data.name,
              },
            });
          } catch (error) {
            console.error("Weather fetch failed:", error);
            set({
              temperature: {
                current: 0,
                low: 0,
                high: 0,
                location: "Location access denied",
              },
            });
          }
        },
        (error) => {
          console.error("Geolocation error:", error);
          set({
            temperature: {
              current: 0,
              low: 0,
              high: 0,
              location: "Location access denied",
            },
          });
        },
        {
          enableHighAccuracy: true,
          timeout: 5000,
          maximumAge: 0,
        }
      );
    } else {
      console.log("Geolocation is not supported by this browser.");
    }
  },
}));

if (typeof window !== "undefined") {
  window.addEventListener("resize", useStore.getState().updateIsMobile);
}

export default useStore;
