import React, { useState } from "react";
import useStore from "../../store";

const TodolistCard = () => {
  const { User, List, setList, delList, toggleCheck } = useStore();
  const [InputValue, setInputValue] = useState("");

  const Submit = (event) => {
    event.preventDefault();
    if (InputValue.trim()) {
      setList(InputValue);
      setInputValue("");
    }
  };

  const InputChange = (event) => {
    setInputValue(event.target.value);
  };

  return (
    <div className="todoListCard">
      <p className="state f-s">
        <span className="list"></span> on going <span className="list">|</span>
        <span className="list yellow"></span> finished
      </p>
      <h3 className="h-3">TO-DO LIST</h3>
      <div className="line-h25">
        {User === "Guest" ? (
          <>
            <p className="f-b f-s">{User}, .</p>
            <p className="f-s">    ! </p>
          </>
        ) : (
          <>
            <ul style={{ marginBottom: "10px" }}>
              {List.map((item, index) => (
                <li
                  key={index}
                  className={item.checked ? "checked" : ""}
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <div
                    style={{
                      width: "230px",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                    }}
                    onClick={() => toggleCheck(index)}
                  >
                    <span className="list"></span>
                    <span>{item.text}</span>
                  </div>
                  <span
                    style={{ textAlign: "right" }}
                    onClick={() => delList(index)}
                  >
                    x
                  </span>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>
      <form onSubmit={Submit} style={{ borderBottom: "1px solid white" }}>
        <div className="line-h25">
          {User === "Guest" ? null : (
            <input
              type="text"
              placeholder="Write down your list"
              style={{ marginTop: "10px" }}
              className="todoInput"
              value={InputValue}
              onChange={InputChange}
            />
          )}
        </div>
      </form>
    </div>
  );
};

export default TodolistCard;
